import { ChessInstance, ShortMove, Square } from "chess.js";
import Chessboard from 'chessboardjsx';
import React, { useEffect, useRef, useState } from 'react';
import tmi from 'tmi.js';
import './App.css';
import UserList from './Components/UserList/UserList';
import { userProperties } from './Components/UserList/UserObject';
const Chess = require('chess.js');

interface PuzzleInterface {
  id: string;
  fen: string;
  moves: string[];
  rating: number;
  deviation: number;
  popularity: number;
  nbplays: number;
  tags: string[];
  url: string;
}

function ReactPushToArray(array: any[], to_add: any) {
  let array_copy = array;
  array_copy.push(to_add);
  return array_copy;
}

function App() {

  const [Heading] = useState<{ [key: number]: string }>({
    1: "Schachmatt in einem Zug!",
    2: "Schachmatt in zwei Zügen!",
    3: "Schachmatt in drei Zügen!",
    4: "Schachmatt in vier Zügen!",
    5: "Schachmatt in fünf Zügen!",
    6: "Finde den besten Zug!"
  });

  const [MateIn, setMateIn] = useState(0);
  const [Users, setUsers] = useState<userProperties[]>([]);
  const [MainChessClass] = useState<ChessInstance>(new Chess());
  const [CompareChessClass] = useState<ChessInstance>(new Chess());
  const [FEN, setFEN] = useState("");
  const [Puzzle, setPuzzle] = useState<PuzzleInterface | null>(null);
  const [/*Orientation*/, setOrientation] = useState<"white" | "black">("white") // Due to bugs in chessboard.jsx
  const [squareStyles, setsquareStyles] = useState({});
  const [PuzzleIndex, setPuzzleIndex] = useState(10);
  const [ToSolveMove, setToSolveMove] = useState("");
  const [HeadlineMate, setHeadlineMate] = useState(0);
  const ToSolveMoveRef = useRef(ToSolveMove);
  const PuzzleIndexRef = useRef(PuzzleIndex);
  const MateInRef = useRef(MateIn);

  // Refferences

  useEffect(() => {
    MateInRef.current = MateIn;
  }, [MateIn])

  useEffect(() => {
    ToSolveMoveRef.current = ToSolveMove;
  }, [ToSolveMove])

  useEffect(() => {
    PuzzleIndexRef.current = PuzzleIndex;
  }, [PuzzleIndex]);

  // Effects 
  useEffect(() => {

    if (Puzzle) {
      setsquareStyles({});
      setPuzzleIndex(0);
      loadFEN(Puzzle.fen);
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [Puzzle])

  useEffect(() => {

    getNewPuzzle();
    let channel = new URLSearchParams(window.location.search).get('c');
    if (channel) {
      TwitchHandler(channel);
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [])

  useEffect(() => {

    if (Puzzle) {
      if (Puzzle.moves[PuzzleIndex]) {
        CompareChessClass.move(UCItoObject(Puzzle.moves[PuzzleIndex]));
        setToSolveMove(() => CompareChessClass.history()[0].replace(/[#]|[+]+/g, ""));

        if (PuzzleIndex % 2 === 0) {
          setTimeout(() => {
            makeMove(UCItoObject(Puzzle.moves[PuzzleIndex]));
          }, (PuzzleIndex === 0) ? 1 : 1000);
        }

      } else {

        setTimeout(() => {
          getNewPuzzle();
        }, 2500);

      }
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [PuzzleIndex]);

  // Functions

  function loadFEN(fen: string) {

    MainChessClass.load(fen);
    CompareChessClass.load(fen);

    updateChessBoard();

  }

  function updateChessBoard() {

    setFEN(MainChessClass.fen());

  }

  function highlightMove(move: ShortMove) {

    if (move) {
      setsquareStyles({
        [move.from]: { backgroundColor: "rgba(22, 164, 138, 0.5)" },
        [move.to]: { backgroundColor: "rgba(22, 164, 138, 0.5)" }
      });
    }

  }

  function makeMove(move: ShortMove) {

    setTimeout(() => {

      highlightMove(move);
      setPuzzleIndex((prev) => prev + 1);
      MainChessClass.move(move);
      CompareChessClass.load(MainChessClass.fen());
      setOrientation((MainChessClass.turn() === "w") ? "white" : "black");
      updateChessBoard();

    }, 500)

  }

  async function getNewPuzzle() {

    let request = (MateInRef.current !== 0) ? MateInRef.current : Math.floor(Math.random() * 5) + 1;
    setHeadlineMate(() => request);
    setPuzzle(await (await fetch("https://api.choozn.ml:2053/chess?m=" + request)).json());

  }

  function UCItoObject(move: string): ShortMove {

    let prevpos: Square = (move.substring(0, 2).toLowerCase() as Square);
    let nextpos: Square = (move.substring(2).toLowerCase() as Square);
    return { from: prevpos, to: nextpos };

  }

  function GetRandomProfileImage() {

    const req = require.context("./images/profiles/", true, /^\.\/.*\.svg$/);
    var svg = req.keys()[Math.floor(Math.random() * req.keys().length)];
    return req(svg).default;

  }

  function AddUser(userName: string) {

    if (userName) {
      if (![...Users].map(user => user.name).includes(userName)) {

        let img = GetRandomProfileImage();
        setUsers([...Users]);
        setUsers(ReactPushToArray(Users, { name: userName, img: img, points: 0 }));

      }
    }

  }

  function AddUserPoint(userName: string) {

    if (Users.map(user => user.name).includes(userName)) {
      let userIndex = [...Users].map((user, index) => user.name).indexOf(userName);
      let array_copy = Users;
      array_copy[userIndex].points = array_copy[userIndex].points + 1;
      setUsers([...Users])
      setUsers(array_copy);
    } else {
      AddUser(userName);
      AddUserPoint(userName);
    }

  }

  function HandleInputMove(userName: string | undefined, message: string) {

    if (userName) AddUser(userName);

    if (MainChessClass.moves().map(move => move.replace(/[#]|[+]+/g, "")).includes(message) && userName) {

      MainChessClass.move(message);

      if ((message === ToSolveMoveRef.current || MainChessClass.game_over()) && PuzzleIndexRef.current % 2 !== 0) {
        let history = MainChessClass.history({ verbose: true })
        let move = history[history.length - 1];

        makeMove(move);
        AddUserPoint(userName!);
      } else {

        MainChessClass.undo();

      }
    }

  }

  function TwitchHandler(channel: string) {

    const client = new tmi.Client({
      connection: {
        secure: true,
        reconnect: true
      },
      channels: [channel]
    });

    client.connect();

    client.on('message', async (channel, tags, message, self) => {

      var userName = tags["display-name"];
      var mod = (tags.mod || tags['user-type'] === 'mod' || channel.slice(1) === userName || userName === "ch00zn");

      if (mod) {
        let command = message.split(" ");
        switch (command[0]) {

          case "!reload":
          case "!skip":
            updateChessBoard();
            getNewPuzzle();
            break;
          case "!mode":
            if (typeof command[1] === "string") {
              switch (command[1]) {
                case "best":
                  setMateIn(6);
                  break;
                case "random":
                  setMateIn(0);
                  break;
              }
            }
            let mode = Math.max(1, Math.min(parseInt(command[1]), 5));
            if (mode) {
              setMateIn(mode);
            }
            break;
          default:
            HandleInputMove(userName, message);
            break;
        }

      } else {
        HandleInputMove(userName, message);
      }

    });

  }

  return (
    <div className="App">
      <div className="game">
        <h1>{Heading[HeadlineMate]}</h1>
        <div className="main">
          <div className="placeholder" id="left">
            <h2>Spieler</h2>
            <hr />
            <UserList users={(Users.sort((a, b) => (b.points - a.points))) ? Users : []} />

          </div>
          <div className="ChessBoardWrapper">
            <Chessboard squareStyles={squareStyles} transitionDuration={400} width={650} allowDrag={() => false} position={FEN} />
          </div>

          <div className="placeholder" id="right">

            <h2>Züge</h2>
            <hr />
            <h4>{(MainChessClass.turn() === "w") ? "Weiß" : "Schwarz"} am Zug</h4>
            <div className="moves">
              {
                MainChessClass.moves().map(move => (<div>  {move.replace(/[#]|[+]+/g, "")} </div>))
              }
            </div>

          </div>
        </div>
      </div>
    </div>
  );


}

export default App;
